  getBaskets(ids) {
    const baskets = [];
    for (let i = 0; i < ids.length; i++) {
      for (let j = 0; j < this.products.length; j++) {
        if (ids[i] === this.products[j].id) {
          baskets.push(this.products[j]);
        }
      }
    }
    return baskets;
  },
